.simpleMain {
    width: 100%;
    height: 50rem;
}

.dropdownSimple {
  width: 30%;
}

.buttonContainer {
    margin-top: 10rem;
    margin-right: auto;
    margin-left: auto;
  }
  
  .buttonMaterial {
    margin-right: 10rem;
    border-radius: 4px;
    padding: 0.7rem 1.5rem;
    background-color: #537dcc;
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    transition: background-color .5s;
  }
  
  .buttonMaterial:hover {
    cursor: pointer;
    background-color: #1d63e6;
  }
  
  .divButton {
    margin-right: auto;
    margin-left: auto;
  }