.mainMaterial {
    position: absolute;
    left: 31%;
    display: flex;
    flex-direction: column;
    font-family: 'Quicksand', sans-serif;
    width: 69%;
    height: 680px;
  }

.buttonContainer {
  margin-top: 10rem;
  margin-right: auto;
  margin-left: auto;
}

.buttonMaterial {
  margin-right: 10rem;
  border-radius: 4px;
  padding: 1rem 1.7rem;
  background-color: #537dcc;
  color: #ffffff;
  font-size: 22px;
  font-weight: 600;
  transition: background-color .5s;
}

.buttonMaterial:hover {
  cursor: pointer;
  background-color: #1d63e6;
}

.divButton {
  margin-right: auto;
  margin-left: auto;
}