.Building {
  display: flex;
  flex-direction: column;
  font-family: 'Quicksand', sans-serif;
  width: 31%;
  height: 680px;
}

.Building .image-container {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
  margin-top: 1rem;
  max-width: 30rem;
  max-height: auto;
  border-style: solid;
  border-width: 2px;
}

.Building h2 {
  margin-left: 1rem;
  position: relative;
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Building-information {
  display: flex;
  justify-content: space-between;
}

.Building-information p {
  font-size: .88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Building-address {
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
